/*@import url(./menuFichiers.css);
@import url(./buttons.css); */

*,
*::before,
*::after {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;

}

html {
  margin: 0px;
  padding: 0px;
  width: 100%;
}

body {
  background: #fff;
  display: flex;
  justify-content: center;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

.bandeau {
  display: flex;
  position: fixed;
  background: rgb(16, 58, 18);
  padding-bottom: 115px;
  text-align: center;
  top: 0px;
  width: 100%;
  height: 0px;

  color: white;
  /*border-radius: 0 0 10px 10px ;
    box-shadow: 0px 0px 10px black;*/
  z-index: 5000;
}

.labelBandeau {
  font-size: 2.0em;
  font-weight: 80;
  color: white;
  display: block;
  margin: 0 auto;
  width: 95%;
  position: relative;
  top: 30px;
}

h1 {
  width: 100%;
  font-size: 2.0em;
  font-weight: 80;
  /*color: rgb(16, 58, 18);*/
}

img {
  position: absolute;
  width: 100px;
  top: 10px;
  right: 2%;

}

.boxForm {
  top: 105px;
  margin-top: 30px;
  padding: 50px;
  width: 90%;
  height: fit-content;
  position: relative;
  background: #f8f9fa;
  border-radius: 10px;
  z-index: 0;
}



::placeholder {
  color: rgb(166, 166, 166);
  font-size: 0.9em;
}


select,
textarea {
  display: block;
  margin: 20px auto;
  outline: 0;
  background: white;
  width: 95%;
  border: 0;
  border-color: #76b998;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  border-radius: 3px;
}


.labelTitre,
.labelSelect,
.labelTxt,
.labelZone,
.labelQuestionTxt {
  color: #5a6268;
  display: block;
  margin: 0 auto;
  width: 95%;
  position: relative;
  top: 7px;
}

h3,
.labelTitre,
.labelZone {
  text-transform: uppercase;
}

.labelQuestionTxt {
  text-transform: none;
}

.labelQuestionTxtTest {
  color: purple;
  position: relative;
  top: 8px;
}

.flex_n_Inputs {
  display: flex;
  justify-content: space-between;
  width: 95%;
  margin: 0 auto;
}

.flexInp {
  margin: 20px auto;
  outline: 0;
  background: white;
  width: 90%;
  border: 0;
  padding: 15px;
  font-size: 14px;
  border-radius: 3px;
}

.flexLeftInp,
.flexRightInp {
  flex-basis: 45%;
}

.labelFlex {
  color: #5a6268;
  position: relative;
  top: 8px;
}

#sous-titre {
  display: inline-block;
  color: white;
}

.flexImgInp {
  margin-bottom: 20px;
}

#dropArea {
  padding: 20px;
  text-align: center;
  position: relative;
}

#fileslbl {
  display: block;
  margin-bottom: 10px;
}

#fileslbl,
#progress {
  background-color: white;
  padding: 10px;
  border: 1px solid white;
  border-radius: 5px;
}

